<template>
	<div>
		
		
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
			<div class="header_wrap underLineGray">
				<h1 style="padding-left:5%;">게시판</h1>
			</div>
			</header>
			<!-- header -->
			<!-- content -->
			<div id="contents">
				<div class="board_view">
					<h6>{{getBoardDetail.boardTitle}}</h6>
					<p class="viewarea_date">{{YYYYMMDDDate(getBoardDetail.regDate)}}</p>
					<div class="viewarea">
						<div class="fileArea"  v-if="fileExistCheck">
							<template v-for="(file,index) in getBoardDetail.boardFile" >
								<a v-if="file.fileType !=='PICTURE'" :key="index" @click="downloadItem(file)" >{{file.fileOriginalName}}
									<br>
								</a>
							</template>
						</div>

						
						<template v-for="(file,index) in getBoardDetail.boardFile" >

							<Images v-if="file.fileType ==='PICTURE'" :key="index"
								:height="250"
								:width="250"
								:src="file.filePath">
							</Images>
						</template>
						
                    	<span v-html="changeTxtToHtml(getBoardDetail.boardContent)"></span>
					</div>
				</div>
				<div class="btn_next_area"><div class="btn_next_area_inner"><button class="btn_list rigthspace" @click="list">목록</button></div></div>
			</div>
			<!-- //content -->
			<!-- footer -->
				<Footer/>
			<!-- //footer -->
		</div>
		<!--// wrap -->
	</div>
</template>
<script>
import  Footer from '@/components/common/Footer'
import Images from "@/components/common/Images";
import  Search from '@/components/board/Search'
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
  name: 'ObituaryReg',
	mixins: [myMixin],
  components: { 
	  Footer,
	  Search
	},
  	props: {
  	},
    data(){
        return{
			id: this.$route.query.id ? this.$route.query.id : '',
		}
	},
	computed:{
		...mapGetters(['getBoardDetail']),
		fileExistCheck(){
			if(this.getBoardDetail.boardFile!==null && this.getBoardDetail.boardFile!==undefined && this.getBoardDetail.boardFile.length>0){
				for(let i=0;i<this.getBoardDetail.boardFile.length;i++){
					if(this.getBoardDetail.boardFile[i].fileType !=='PICTURE'){
						return true
					}
				}
			}
			return false
		}
	},
   	mounted() {
		   const parameter = {
			   id: this.id
		   };
		    this.$store.dispatch('REQUEST_BOARD_DETAIL',parameter)
	},
	beforeDestroy(){
		this.$store.commit('BOARD_INIT')
	},
	methods:{
		list(){
			
            this.$router.go(-1)
		}
	}
}
</script>

